footer{
    position: relative;
    background: $dark-blue;

    .emaar-logo{
        position: absolute;
        bottom: 15px;
        right: 15px;
    }
}

.footer{

    &-logo{
        padding: 55px 0 40px 0;
        text-align: center;
    }

    &-title{
        @include fmedium();
        margin-bottom: 20px;
        font-size: 18px;
        color: $white;
        &.upperCase {
            text-transform: uppercase;
        }
    }   

    &-ul{
        ul{
            list-style: none;
            padding-left: 0px;
    
            li{
                margin-bottom: 5px;
                a{
                    font-size: 15px;
                    color: $white;
                    @include flight();
                }
            }
        }

        &.icons{
            ul{
                li{
                    margin-bottom: 20px;

                    a{
                        position: relative;
                        padding-left: 36px;

                        @include respond-to('large'){
                            display: block;
                        }
                        
                        @include respond-to('medium'){
                            padding-left: 33px;
                            
                        }
                        &.underlined {
                            text-decoration: underline;
                        }
                        i{
                            transform: translateY(-50%);
                            position: absolute;
                            left: 0;
                            top: 50%;

                            svg{
                                display: block;
                            }
                        }
                    }
                }
            }
        }
    }

    &-copy{ 
        border-top: 1px solid #979797;
        padding: 14px 0 12px 0;
        @include clearfix();

        @media only screen and (min-width: 768px) and (max-width: 991px) {
            margin-right: 67px;
        }

        @include respond-to('medium'){
            padding:24px 72px 12px 0;
        }

        ul{
            list-style: none;
            margin-bottom: 0;
            padding-left: 0;
            float: left;
            @include clearfix();

            @include respond-to('medium'){
                margin-bottom: 10px;
                float: none;
            }

            li{
                margin-right: 5px;
                float: left;

                a{
                    position: relative;
                    padding-left: 9px;
                    font-size: 12px;    
                    color: $white;
                    opacity: .6;

                    &:before{
                        transform: translateY(-50%);
                        position: absolute;
                        content: '-';
                        top: 50%;
                        left: 0;
                    }

                    &:hover{
                        opacity: 1;

                        &:before{
                            opacity: .6;
                        }
                    }
                }

                &:first-child{
                    a{
                        padding-left: 0;

                        &:before{
                            display: none;
                        }
                    }
                }
            }
        }

        .rights{
            margin-bottom: 0px;
            font-size: 12px; 
            float: right;
            color: $white;
            opacity: .6;

            @include respond-to('medium'){
                float: none;
            }
        }
    }
}

// special column adjustments for mobile only

@include respond-to('medium'){
    footer{
        padding-left: 15px;
    }
    .footer{
        
        &-ul,
        &-copy{
            margin-left: -15px;
        }
    }
}