.map-item {
    position: absolute;
    height: 1px;
    width: 1px;

    @include respond-to('medium'){
		z-index: 11;
		&.popup-active {
			z-index: 12;
		}
	}
	
  }

  .mapMainimage {
    width: 1366px;
    height: 647px
  }
  
  .map-item-link {
    display: flex;
    flex-wrap: wrap;
    position: absolute;
    text-align: center;
    font-size: 18px;
    height: 97px;
    width: 140px;
    color: #fff;
    left: -70px;
    top: 0;

    &:before {
        background: rgba(255,255,255,.15);
        transition: all ease 0.2s;
        border-radius: 51px;
        position: absolute;
        height: 51px;
        content: '';
        width: 51px;
        left: 45px;
        top: 0px;
      }

    &:after {
        background: rgba(255,255,255,.3);
        transition: all ease 0.2s;
        border: 1px solid #fff;
        border-radius: 100px;
        position: absolute;
        content: '';
        height: 27px;
        width: 27px;
        left: 57px;
        top: 12px;
    }

    .map-item-text {
        position: absolute;
        min-width: 140px;
        bottom: 0;
        left: 0;
    }

    &:hover{
        &:before {
            background: rgba(255,255,255,.55);
        }
        &:after {
            background: rgba(255,255,255,.7);
        }

        .map-item-text {
            color: #fff;
        }
    }

    &:focus{
        .map-item-text {
            color: #fff;
        }
    }
}
  

.map-item-popup {
    display: block !important;
    width: 486px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    visibility: hidden;
    transition: opacity .2s linear, visibility .2s linear;
    z-index: 199;

    .close-ico {
        text-indent: -9999px;
        position: absolute;
        overflow: hidden;
        height: 30px;
        right: 18px;
        width: 30px;
        top: 21px;

        &:before,
        &:after{
            content: '';
            position: absolute;
            top: 12px;
            right: 0;
            left: 0;
            height: 2px;
            background: #fff;
            transform: rotate(45deg);
            transition: background .2s linear;
        }

        &:after{
            transform: rotate(-45deg);
        }

        &:hover{
            &:before,
            &:after{
                // background: rgba(255, 255, 255, 0.5);
            }
        }
    }       
}
  
.map-item-popup-content {
    background: #fff;
    border-radius: 5px;
    overflow: hidden;
    &-img{
        img{
            height: auto;
            width: 100%;
        }
    }

    &-text{
        padding: 35px 39px;

        h3 {
            font-size: 30px;
            margin: 0 0 20px;
        }

        p {
            color: #64615B;
            margin: 0;
        }
    }
}
  
  
.popup-active .map-item-popup {
    visibility: visible;
    opacity: 1;
}
  
  
  @media (max-width: 767px) {
    .map-item-popup {
      width: 338px;
    }
    .map-item-popup .close-ico {
      top: 9px;
      right: 7px;
      width: 25px;
      height: 25px;
    }
    .map-item-popup-content-text {
      padding: 22px;
    }
    .map-item-popup-content-text h3 {
      margin: 0 0 10px;
    }
  }
  
.map-section {
    position: relative;

    .title-holder{
        .heading{
            @extend .h1;
            text-align: center;
            color: #fff;
            margin: 0;
        }
    }
}
  
.map-section-inner {
    position: relative;
    overflow: hidden;
}
  
.map-section-frame {
    position: relative;
  
}
  
.map-section-frame:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0);
  }
  
  .map-section .container {
    position: absolute;
    top: 102px;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    z-index: 10;
}
  
  .map-section img {
    width: 100%;
  }
  
  .map-section img.map-section-swipe-icon {
    display: none;
    position: absolute;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    width: 36px;
    height: 38px;
    pointer-events: none;
    z-index: 1;
  }
  
  @media (max-width: 1280px) and (min-width: 1024px) {
    .map-section .container {
      top: 66px;
    }
  }
  
  @media (max-width: 1023px) {
    .map-section img.map-section-swipe-icon {
      display: block;
    }
    .map-section-inner {
      overflow-x: auto;
    }
    .map-section-frame {
      width: 1193px;
      height: 653px;
    }
    .map-section .container {
      top: 66px;
    }
   
    .map-section img {
      max-width: none;
      display: block;
    }
}


// map popup active

.active-popup{
    body{
        overflow: hidden !important;
        position: relative;

        &:before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0,0,0,.5);
            z-index: 101;
        }
    }
}