.event-section{
    @extend .section-top-padding;
    @extend .section-bottom-margin;

    @include respond-to('medium'){
        padding-top: 38px;
    }
}

.event{
    
    &-item{
        position: relative;
        display: flex;
        box-shadow: 0 0 8px 0 #e4e4e4;
        border-radius: 5px;
        margin-bottom: 20px;
        overflow: hidden;

        &:hover{
            text-decoration: none;
            box-shadow: 0px 0px 2px 0 #ccc;
        }
    }

    .datesec{
        background: #818b96;
        text-align: center;
        padding: 11px 0;
        color: #fff;
        width: 10%;
        @include fmedium;
        min-width: 68px;


        .date{
            display: block;
            font-size: 29px;
            line-height: 1;
        }

        .day{
            display: block;
            line-height: 1;
        }
    }

    .descSec{
        display: flex;
        align-items: center;
        padding: 10px 20px;
        width: 90%;

        .title{
            margin-bottom: 0px;
            @include fmedium;
            color: $copy;
        }

    }
}