// 0.1 Global styling Starts here
//----------------------------------------
body{
    /*!rtl:raw: direction: rtl; */
    font-family: $font-family-light;
    font-size: 17px; 
    &.ov-none{
        overflow: hidden;
    }
    
    &.overflow {
        overflow: hidden;
    }
}


.arabic{
    font-family: 'Cairo', arial, sans-serif;
}

p{
    line-height: 1.6;
}

.btn-primary{
    padding: 11px 50px 12px 22px;
    background-color: transparent;
    border: 2px solid $yellow;
    border-radius: 25px;
    position: relative;
    color: $yellow;
    @include fmedium();

    i{
        transform: translateY(-50%);
        position: absolute;
        right: 21px;
        top: 50%;

        svg{
            display: block;    
        }
    }

    &:hover,
    &:active{
        background-color: $yellow;
        border-color: $yellow;
        color: $white;

        i{
            svg{
                path{
                    fill: $white;
                }
            }
        }
    }

    &:focus{
        box-shadow: 0 0 0 0.2rem rgba(243,179,41,.5);
    }

    &.no-arrow{
        padding: 11px 22px 12px 22px;
    }
}

.btn-primary:not(:disabled):not(.disabled).active,
.btn-primary:not(:disabled):not(.disabled):active,
.show>.btn-primary.dropdown-toggle{
    background-color: $yellow;
    border-color: $yellow;
    color: $white;
}

.btn-primary:not(:disabled):not(.disabled).active:focus,
.btn-primary:not(:disabled):not(.disabled):active:focus,
.show>.btn-primary.dropdown-toggle:focus{
    box-shadow: 0 0 0 0.2rem rgba(243,179,41,.5);
    
}

// loadmore btn

.lm-btn-wrap{
    text-align: center;
    margin: 40px 0;
}

.btn-loadmore{
    background: rgba(136,124,104,.19);
    padding-bottom: 0;
    line-height: 50px;
    min-width: 237px;
    padding-top: 0;
    color: $copy;
    height: 50px;
    @include fmedium;

    &:hover{
        background: rgba(136,124,104,.29);
        color: $copy;
    }
}

.headerSpace{
    height: 74px;
}

.section-header{
    margin-bottom: 36px;
    @include fmedium;
    
    .heading{
        margin-bottom: 24px;
        color: $heading;
        @include flight;
    }

    p{
        font-size: 24px;
        line-height: 1.3;
    }
}
