.media-section{
    @extend .section-top-padding;
    @extend .section-bottom-margin;

    @include respond-to('medium'){
        padding-top: 38px;
    }
}

.media-header{
    border-top: 1px solid #d8d8d8;
    position: relative;
    padding: 40px 0;

    .heading{
        margin-bottom: 0px;
        padding-right: 90px;
        font-size: 30px;
    }

    .link{
        position: absolute;
        padding: 5px 22px 5px 22px;
        @include fmedium;
        color: $copy;
        top:39px;
        right: 0;
    

        i{
            transform: translateY(-50%);
            position: absolute;
            right: 0px;
            top: 50%;

            svg{
                display: block;

                path{
                    fill: $copy;
                }
            }
        }
    }
}

.media-event{
    padding-bottom: 20px;
}