.content-section{
    @extend .section-top-padding;
    @extend .section-bottom-margin;
    @include fmedium;

    @include respond-to('medium'){
        padding-top: 38px;
    }


    h2{
        margin-bottom: 20px;
    }

    h3{
        margin-bottom: 20px;
        @include fmedium;
    }

    ul,
    ol{
        padding-left: 18px;

        li{
            margin-bottom: 35px;
        }
    }
}

.left-img-content{
    float: left;
    margin-right: 30px;
    margin-bottom: 30px;

    @include respond-to ('large'){
        margin-bottom: 15px;
        margin-right: 0;
        float: none;
    }

    @include respond-to ('medium'){
        width: 100%;
    }
}

.right-img-content{
    float: right;
    margin-left: 30px;
    margin-bottom: 30px;

    @include respond-to ('large'){
        margin-bottom: 15px;
        margin-left: 0;
        float: none;
    }

    @include respond-to ('medium'){
        width: 100%;
    }
}

.image-left-section,
.image-right-section{
    @include clearfix;
}

.full-image{
    margin: 10px 0 30px;
    width: 100%;

    @include respond-to ('medium'){
        width: calc(100% + 30px);
        margin-left: -15px;
    }
}

.table{ 
    &.custom-style{
        text-align: center;

        thead{
            background-color: rgba(216,216,216,.37);

            th{
                @include respond-to('medium'){
                    min-width: 150px;
                }
            }
        }
    }
}


.amenitiesBox{
    margin-top: 30px;
    margin-bottom: 20px;
}

.icons-lists{
    align-items: flex-start;
    padding-left: 0 !important;
    flex-wrap: wrap;
    list-style: none;
    display: flex;
    margin-top: 40px;

    li{
        width: 20%;

        @include respond-to ('large'){
            width: 25%;
        }

        @include respond-to ('medium'){
            width: 33.3%;
        }

        .icon{
            text-align: center;
            height: 50px;
            margin-bottom: 5px;
        }

        .title{
            text-align: center;
        }
    }
}


.accordion{
    &.custom-style{
        .accordionWrap{
            border-bottom: 1px solid #D8D8D8;
            border-top: 1px solid #D8D8D8;
            margin-top: -1px;

            .accordionHeading{

                button{
                    background: $white;
                    border: none;
                    width: 100%;
                    padding: 17px 30px 17px 0px;
                    text-align: left;
                    color: $copy;
                    font-size: 17px;
                    position: relative;
                    cursor: pointer;

                    &.collapsed{
                        &:after{
                            opacity: 1;

                        }
                    }

                    &:before{
                        transform: translateY(-50%);
                        background: #414850;
                        position: absolute;
                        border-radius: 2px;
                        right: 3px;
                        width: 12px;
                        height: 2px;
                        content: '';
                        top: 50%;
                        
                    }

                    &:after{
                        transform: translateY(-50%);
                        background: #414850;
                        position: absolute;
                        border-radius: 2px;
                        height: 12px;
                        right: 8px;
                        content: '';
                        width: 2px;
                        top: 50%;

                        opacity: 0;
                    }
                }
            }

            .card-body{
                padding: 10px 0 20px 0;
            }
        }
    }
}

.exploreBoxes{
    margin: 30px 0 50px;

    .explore-item{
        overflow: hidden;

        @include respond-to('large'){
            margin-bottom: 30px;
        }

        @include respond-to('large'){
            margin-bottom: 15px;
        }
    }
}
