// text with image
.text-img-box{
    padding: 50px 0;

    .textWrap{
        padding-right: 50px;

        @include respond-to('medium'){
            padding-right: 0px;
        }

        .heading{
            @extend .h1;
            margin-bottom: 40px;
        }
    }

    .imgWrap{
        img{
            width: 100%;
        }
    }

    &.left-img{
        .textWrap{
            padding-left: 50px;

            @include respond-to('medium'){
                padding-left: 0px;
            }
        }
    }
}

// text with background image
.bg-image-text{
    background-size: cover!important;
    position: relative;
    padding: 100px 0;

    @include respond-to('medium'){
        background-attachment: scroll !important;
    }

    &:before{
        background: -moz-linear-gradient(left, rgba(0,0,0,0.72) 0%, rgba(125,185,232,0.03) 96%, rgba(125,185,232,0) 100%);
        background: -webkit-linear-gradient(left, rgba(0,0,0,0.72) 0%,rgba(125,185,232,0.03) 96%,rgba(125,185,232,0) 100%);
        background: linear-gradient(to right, rgba(0,0,0,0.72) 0%,rgba(125,185,232,0.03) 96%,rgba(125,185,232,0) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#b8000000', endColorstr='#007db9e8',GradientType=1 );
        position: absolute;
        height: 100%;
        content: '';
        width: 100%;
        z-index: 2;
        left: 0;
        top: 0;
    }

    &:after{
        /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#7db9e8+0,000000+85,000000+100&0+0,0.7+100 */
        background: -moz-linear-gradient(top, rgba(125,185,232,0) 0%, rgba(0,0,0,0.6) 85%, rgba(0,0,0,0.7) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top, rgba(125,185,232,0) 0%,rgba(0,0,0,0.6) 85%,rgba(0,0,0,0.7) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom, rgba(125,185,232,0) 0%,rgba(0,0,0,0.6) 85%,rgba(0,0,0,0.7) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#007db9e8', endColorstr='#b3000000',GradientType=0 ); /* IE6-9 */
        position: absolute;
        height: 100%;
        width: 100%;
        content: '';
        bottom: 0;
        z-index: 1;
        left: 0;
    }

    .text-wrap{
        position: relative;
        max-width: 449px;
        color: $white;
        z-index: 3;

        .heading{
            @extend .h1;
            margin-bottom: 30px;
            color: $white;
        }

        p{
            color: $white;
        }
    }
}

.explore{
    background: $black;
    padding: 80px 0;

    .heading{
        color: $white;
        @extend .h1;
        margin-bottom: 30px;
    }

    @include respond-to('medium'){
        padding: 40px 0;
    }
}

.explore-item{
    // position: relative;

    .link{
        position: relative;
        display: block;

        .explore-item-img{
            position: relative;

            .img-fluid{
                width: 100%;
            }
    
            .title{
                position: absolute;
                padding: 15px 20px;
                @include fmedium;
                margin-bottom: 0;
                font-size: 20px;
                color: $white;
                width: 100%;
                z-index: 1;
                bottom: 0;

                transition:opacity .4s linear;
                opacity:1;
            }
    
            &:after{
    
                /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#17222a+0,485459+100&0+0,0.96+100 */
                background: -moz-linear-gradient(top, rgba(23,34,42,0) 0%, rgba(13, 14, 14, 0.96) 100%); /* FF3.6-15 */
                background: -webkit-linear-gradient(top, rgba(23,34,42,0) 0%,rgba(13, 14, 14, 0.96) 100%); /* Chrome10-25,Safari5.1-6 */
                background: linear-gradient(to bottom, rgba(23,34,42,0) 0%,rgba(13, 14, 14, 0.96) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
                filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0017222a', endColorstr='#f5485459',GradientType=0 ); /* IE6-9 */
    
                position: absolute;
                height: 154px;
                content: '';
                width: 100%;
                bottom: 0;
                left: 0;
            }
        }

        .explore-item-text{
            /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#17222a+0,000000+78,000000+100&0.74+0,0.9+78,0.9+100 */
            background: -moz-linear-gradient(top, rgba(23,34,42,0.74) 0%, rgba(0,0,0,0.7) 78%, rgba(0,0,0,0.9) 100%); /* FF3.6-15 */
            background: -webkit-linear-gradient(top, rgba(23,34,42,0.74) 0%,rgba(0,0,0,0.7) 78%,rgba(0,0,0,0.9) 100%); /* Chrome10-25,Safari5.1-6 */
            background: linear-gradient(to bottom, rgba(23,34,42,0.74) 0%,rgba(0,0,0,0.7) 78%,rgba(0,0,0,0.9) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#bd17222a', endColorstr='#e6000000',GradientType=0 ); /* IE6-9 */
    
            transform: translateY(100%);
            transition: transform .4s;
            position: absolute;
            padding: 25px 20px 15px 20px;
            height: 100%;
            width: 100%;
            z-index: 1;
    
            .title{
                margin-bottom: 22px;
                @include fmedium;
                font-size: 20px;
                color: $white;
            }
    
            ul{
                padding-left: 18px;
                font-size: 16px;
                color: $white;
    
                li{
                    margin-bottom: 5px;
                }
            }
        }

        &:hover{
            .explore-item-img{
                .title{
                    opacity: 0;
                }
            }

            .explore-item-text{
                transform: translateY(-100%);
            }
        }
    }
    
}

// slider style
.exploreSlider{
    position: relative;

    @include respond-to('medium'){
        margin: 0 40px;
    }
    

    .swiper-button-prev,
    .swiper-button-next{
        background-size: 23px 42px;
        height: 42px;
        
        width: 23px;

        &.swiper-button-disabled{
            opacity: 0;
        }
    }

    .swiper-button-next,
    .swiper-container-rtl .swiper-button-prev{
        background-image: url(../images/icon/swiper-right.svg);
        right: -50px;

        @media screen and (min-width: 768px) and (max-width: 1220px){
            right: -30px;
        }

        @include respond-to('medium'){
            right: -40px;
        }

        &:focus{
            outline:none;
        }
    }

    .swiper-button-prev,
    .swiper-container-rtl .swiper-button-next{
        background-image: url(../images/icon/swiper-left.svg);
        left: -50px;

        @media screen and (min-width: 768px) and (max-width: 1220px){
            left: -30px;
        }

        @include respond-to('medium'){
            left: -40px;
        }

        &:focus{
            outline: none;
        }
    }
}