.sitemap-section{
    @extend .section-top-padding;
    @extend .section-bottom-margin;
}

.site-list{
    border-bottom: 1px solid #D8D8D8;
    padding-bottom: 25px;
    margin-top: 40px;

    &:last-child{
        border-bottom: none;
    }

    .parent-page{
        color: $heading !important;
        font-size: 30px;
    }

    .listing{
        padding-left: 40px;
        list-style: none;
        margin-bottom: 0;

        li{
            margin: 20px 0;
            a{
                font-size: 24px;
                color: $copy;
                @include fmedium;
            }
        }
    }
}