.c-banner{
    position: relative;
    display: block;
    .img{
        width: 100%;
    }

    .mob-img{
        width: 100%;
    }

    &:after{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: -moz-linear-gradient(left, rgba(0,0,0,0.65) 0%, rgba(0,0,0,0) 41%);
        background: -webkit-linear-gradient(left, rgba(0,0,0,0.65) 0%,rgba(0,0,0,0) 41%);
        background: linear-gradient(to right, rgba(0,0,0,0.65) 0%,rgba(0,0,0,0) 41%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6000000', endColorstr='#00000000',GradientType=1 );
    }
}

.contactheader{
    text-align: center;
    padding: 40px 0 60px;

    .heading{
        margin-bottom: 12px;
    }

    p{
        @include fmedium;
    }

    @include respond-to('large'){
        text-align: left;
        padding: 40px 0 30px;
    }

    &-icons{
        display: flex;
        justify-content: center;

        @include respond-to('large'){
            display: block;
            justify-content: left;
        }

        .istyle{
            margin: 0 40px;
            position: relative;

            @include respond-to('large'){
                margin: 20px 10px 20px 40px;
            }

            i{
                transform: translateY(-50%);
                position: absolute;
                left: -39px;
                top: 50%;

                svg{
                    display: block;
                    path{
                        fill: $copy;
                    }
                }
            }

            a{
                color: $copy;
                font-size: 24px;
                @include fmedium;
            }
        }
    }
}

.mapsection{
    background: #f1f1f1;
    min-height: 500px;
    position: relative;

    @include respond-to(large){
        padding-bottom: 0px;
    }
}

.mapDesPosition{
    position: absolute;
    width: 100%;
    z-index: 1;
    top: 0;

    .container{
        position: relative;
    }



    @include respond-to(large){
        position: static;
        transform: none;
            .container{
            padding: 0px;
            max-width: inherit;
        }
    }
}

.mapdesc{
    background: rgba(255,255,255,.8);
    box-shadow: 0 0 8px 0 #e4e4e4;
    padding: 30px 15px;
    border-radius: 5px;
    max-width: 365px;

    position: absolute;
    left: 15px;
    top: 54px;

    @include respond-to(large){
        max-width: inherit;
        position: static;
    }

    .heading{
        border-bottom: 1px solid#d8d8d8;
        padding-bottom: 30px;
        margin-bottom: 30px;
        font-weight: 400;
        font-size: 30px;
        @include flight;
    }


    p,.descriptions{
        margin-bottom: 5px;
        @include fmedium;
    }

    .btn{
        margin-top: 20px;
    }
}

.mapPosition{
    position: absolute !important;
    z-index: 1;
    right: 0;
    left: 0;
    top: 0;

    @include respond-to(large){
        position: static !important;
    }
}

#map {
    height: 500px;  /* The height is 400 pixels */
    width: 100%;  /* The width is the width of the web page */
}