.search-section{
    @extend .section-top-padding;
    @extend .section-bottom-margin;
}

.search-field-box{
    margin-bottom: 55px;
    position: relative;

    .input-field{
        border-bottom: 1px solid #D2D2D2;
        padding: 15px 60px 15px 15px;
        border-right: 0;
        border-left: 0;
        border-top: 0;
        width: 100%;
    }

    .submit{
        background: url('../images/icon/search-btn.svg');
        background-position-x: 20px;
        background-position-y: 17px;
        background-repeat: no-repeat;
        color: transparent;
        position: absolute;
        height: 56px;
        width: 64px;
        right: 0;
        top: 0;
        border: none;
        cursor: pointer;

        &:focus{
            border: 1px solid #d2d2d2;
        }
    }
}


.searchresult{
    .search-data{
        margin-bottom: 39px;
        padding-bottom: 30px;
        border-bottom: 1px solid #d2d2d2;

        .title{
            font-size: 24px;
            margin-bottom: 20px;
            color: $copy;
        }
    }
}
