.sideNavigation {
  max-width: 284px;
}

@media screen and (max-width: 767px) {
  .sideNavigation {
    max-width: 100%;
  }
}

.sideNavigation ul {
  list-style: none;
  padding-left: 0px;
  box-shadow: 0 0 8px 0 #e4e4e4;
  border-radius: 5px;
  overflow: hidden;
}

.sideNavigation ul li a {
  padding: 15px 24px;
  position: relative;
  display: block;
  color: #64615B;
  font-family: "Foundry Sans W04 Md", Arial, sans-serif;
}

.sideNavigation ul li a:after {
  background: url("../images/icon/right-arrow-sidebar.svg") no-repeat center;
  transform: translateY(-50%);
  background-size: 100%;
  position: absolute;
  height: 15px;
  content: '';
  right: 20px;
  width: 8px;
  top: 50%;
  display: none;
}

.sideNavigation ul li:first-child {
  border-top: 5px solid #F3B329;
  margin-bottom: 5px;
}

.sideNavigation ul li:first-child a {
  padding: 22px 24px 18px;
  font-size: 24px;
}

.sideNavigation ul li:first-child a:before {
  background: #D8D8D8;
  position: absolute;
  height: 1px;
  width: calc(100% - 48px);
  bottom: 0;
  left: 24px;
  content: '';
}

.sideNavigation ul li.active {
  background: rgba(136, 124, 104, 0.18);
}

.sideNavigation ul li.active a:after {
  display: block;
}

@media screen and (max-width: 767px) {
  .sideNavigation {
    transition: max-height .4s ease-out;
    overflow: hidden;
    padding-top: 0px;
    max-height: 0;
    box-shadow: 0 0 8px 0 #e4e4e4;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
  }
  .sideNavigation ul {
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
    box-shadow: none;
  }
  .sideNavigation ul li:first-child {
    border-top: 0px;
    margin-bottom: 0px;
  }
  .sideNavigation ul li:first-child a {
    padding: 15px 24px;
    font-size: 17px;
  }
  .sideNavigation ul li:first-child a:before {
    display: none;
  }
  .sideNavigation ul li.active {
    display: none;
  }
}

.mobileAccordionHolder {
  background: rgba(136, 124, 104, 0.19);
  margin-top: 34px;
  padding: 15px 24px;
  border-radius: 5px;
  font-family: "Foundry Sans W04 Md", Arial, sans-serif;
  position: relative;
  display: none;
}

