.mobileAccordionHolder:before {
  width: 14px;
  height: 2px;
  background: #414850;
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 25px;
  border-radius: 5px;
}

.mobileAccordionHolder:after {
  height: 14px;
  width: 2px;
  background: #414850;
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 31px;
  border-radius: 5px;
}

.mobileAccordionHolder.active {
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}

.mobileAccordionHolder.active:after {
  opacity: 0;
}

.mobileAccordionHolder.active + .sideNavigation {
  max-height: 500px;
}

