  text-align: center;
  padding: 11px 0;
  color: #fff;
  width: 10%;
  font-family: "Foundry Sans W04 Md", Arial, sans-serif;
  min-width: 68px;
}

.event .datesec .date {
  display: block;
  font-size: 29px;
  line-height: 1;
}

.event .datesec .day {
  display: block;
  line-height: 1;
}

.event .descSec {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  width: 90%;
}

.event .descSec .title {
  margin-bottom: 0px;
  font-family: "Foundry Sans W04 Md", Arial, sans-serif;
  color: #64615B;
}

.site-list {
  border-bottom: 1px solid #D8D8D8;
  padding-bottom: 25px;
  margin-top: 40px;
}

.site-list:last-child {
  border-bottom: none;
}

.site-list .parent-page {
  color: #887C68 !important;
  font-size: 30px;
}

.site-list .listing {
  padding-left: 40px;
  list-style: none;
  margin-bottom: 0;
}

.site-list .listing li {
  margin: 20px 0;
}

.site-list .listing li a {
  font-size: 24px;
  color: #64615B;
  font-family: "Foundry Sans W04 Md", Arial, sans-serif;
}

.search-field-box {
  margin-bottom: 55px;
  position: relative;
}

.search-field-box .input-field {
  border-bottom: 1px solid #D2D2D2;
  padding: 15px 60px 15px 15px;
  border-right: 0;
  border-left: 0;
  border-top: 0;
  width: 100%;
}

.search-field-box .submit {
  background: url("../images/icon/search-btn.svg");
  background-position-x: 20px;
  background-position-y: 17px;
  background-repeat: no-repeat;
  color: transparent;
  position: absolute;
  height: 56px;
  width: 64px;
  right: 0;
  top: 0;
  border: none;
  cursor: pointer;
}

.search-field-box .submit:focus {
  border: 1px solid #d2d2d2;
}

.searchresult .search-data {
  margin-bottom: 39px;
  padding-bottom: 30px;
  border-bottom: 1px solid #d2d2d2;
}

.searchresult .search-data .title {
  font-size: 24px;
  margin-bottom: 20px;
  color: #64615B;
}

@media screen and (max-width: 767px) {
  .gallery-section {
    padding-top: 38px;
  }
}

.gallerygrid {
  margin-left: -5px;
  margin-right: -5px;
}

.gallerygrid .col-lg-4, .gallerygrid .col-sm-6 {
  padding-left: 5px;
  padding-right: 5px;
}

.gallery-item-link {
  margin-bottom: 30px;
  position: relative;
  display: block;
}

.gallery-item-link .img {
  width: 100%;
}

.gallery-item-link .title {
  margin-top: 15px;
  font-family: "Foundry Sans W04 Md", Arial, sans-serif;
  color: #64615B;
}

.gallery-item-link .playicon {
  transform: translate(-50%, -50%);
  position: absolute;
  margin-top: -15px;
  left: 50%;
  top: 50%;
  opacity: .8;
  transition: ease .2s;
}

.gallery-item-link:hover {
  text-decoration: none;
}

.gallery-item-link:hover .title {
  color: #F3B329;
}

.gallery-item-link:hover .playicon {
  transform: translate(-50%, -50%) scale(1.1);
  opacity: 1;
}

.fullscreen-overlay {
  top: 0 !important;
  right: 0 !important;
  left: 0 !important;
  bottom: 0 !important;
  position: absolute !important;
  background-color: #000000 !important;
  width: 100% !important;
  height: 100% !important;
  margin: auto !important;
}

.fullscreen-overlay .fancybox-close {
  /*rtl:ignore */
  right: 0 !important;
  top: 0 !important;
}

.fullscreen-overlay .fancybox-inner {
  width: 100% !important;
}

.fullscreen-overlay .fancybox-skin {
  padding: 0 !important;
  box-shadow: none;
}

@media screen and (max-width: 767px) {
  .download-section {
    padding-top: 38px;
  }
}

.listingygrid {
  margin-left: -5px;
  margin-right: -5px;
}

.listingygrid .col-lg-4, .listingygrid .col-sm-6 {
  padding-left: 5px;
  padding-right: 5px;
}

.download-item {
  margin-bottom: 30px;
}

.download-item .imgholder {
  position: relative;
  overflow: hidden;
}

.download-item .imgholder .img {
  width: 100%;
}

.download-item .imgholder .pdf {
  position: absolute;
  top: 10px;
  left: 10px;
}

.download-item .imgholder .hoverHolder {
  background: rgba(13, 27, 42, 0.7);
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  transition: transform .4s;
  transform: translateY(100%);
}

.download-item .imgholder .hoverHolder .link {
  transform: translateY(-50%);
  text-decoration: none;
  text-align: center;
  position: absolute;
  font-family: "Foundry Sans W04 Md", Arial, sans-serif;
  font-size: 14px;
  color: #fff;
  width: 50%;
  top: 50%;
  left: 0%;
}

.download-item .imgholder .hoverHolder .link + .link {
  left: 50%;
}

.download-item .imgholder .hoverHolder .link .icon {
  margin-bottom: 10px;
  display: block;
}

.download-item .imgholder .hoverHolder .link:hover {
  color: #F3B329;
}

.download-item .imgholder .hoverHolder .link:hover svg g {
  fill: #F3B329;
}

.download-item .imgholder:hover .hoverHolder {
  transform: translateY(0%);
}

.download-item .title {
  margin-top: 15px;
  font-family: "Foundry Sans W04 Md", Arial, sans-serif;
  color: #64615b;
}

.news-item-link {
  margin-bottom: 30px;
  position: relative;
  display: block;
}

.news-item-link .imgholder {
  position: relative;
  overflow: hidden;
}

.news-item-link .imgholder:after {
  background: -moz-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(183, 191, 199, 0.65) 100%);
  background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(183, 191, 199, 0.65) 100%);
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(183, 191, 199, 0.65) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#a6b7bfc7',GradientType=0 );
  content: '';
  height: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  opacity: .8;
}

.news-item-link .imgholder .img {
  width: 100%;
}

.news-item-link .imgholder .date {
  position: absolute;
  bottom: 10px;
  left: 10px;
  z-index: 1;
  color: #fff;
}

.news-item-link .title {
  margin-top: 15px;
  font-family: "Foundry Sans W04 Md", Arial, sans-serif;
  color: #64615B;
}

.news-item-link:hover {
  text-decoration: none;
}

.news-item-link:hover .title {
  color: #F3B329;
}

.c-banner {
  position: relative;
  display: block;
}

.c-banner .img {
  width: 100%;
}

.c-banner .mob-img {
  width: 100%;
}

.c-banner:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: -moz-linear-gradient(left, rgba(0, 0, 0, 0.65) 0%, rgba(0, 0, 0, 0) 41%);
  background: -webkit-linear-gradient(left, rgba(0, 0, 0, 0.65) 0%, rgba(0, 0, 0, 0) 41%);
  background: linear-gradient(to right, rgba(0, 0, 0, 0.65) 0%, rgba(0, 0, 0, 0) 41%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6000000', endColorstr='#00000000',GradientType=1 );
}

