.siteHeader {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    z-index: 1;
    padding: 40px 0 0;
    z-index: 100;
    transition: all ease 0.2s;
    .flexBox {
        &:after,
        &:before {
            content: '';
            clear: both;
        }
        &:after {
            display: table;
        }
        &.searchOn {
            .siteNavigation {
                width: 60%;
                > ul {
                    display:none;
                }
            }
            .siteSearch {
                width: 100%;
                .swrap {
                    border-bottom-color: #fff;
                }
                .searchFielfds {
                    width: 100%;
                }
            }
        }
        .siteLogo {
            float: left;
        }
    }
    .siteLogin {
        position: absolute;
        right: 0;
        top: 26px;
        height: 52px;
        background: $yellow;
        border-top-left-radius: 28px;
        border-bottom-left-radius: 28px;
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 15px;
        text-transform: uppercase;
        transition: all ease 0.2s;
        padding:0 30px;
        @include fmedium();
        img, svg {
            margin-right: 20px;
        }
        &:hover {
            text-decoration: none;
            background: $white;
            color: $yellow;

            svg{
                path{
                    fill: $yellow;
                }
            }
        }
    }
    .hamburgers {
        display: none;
    }

    &.isFixed {
        background: #0d1b2a;
        position:fixed;
        padding:24px 0 0;
        height: 74px;
        -webkit-animation: fixedheader 0.5s 1; /* Safari 4+ */
        -moz-animation:    fixedheader 0.5s 1; /* Fx 5+ */
        -o-animation:      fixedheader 0.5s 1; /* Opera 12+ */
        animation:         fixedheader 0.5s 1; /* IE 10+, Fx 29+ */
        .siteLogin {
            top: 9px;
        }
        .loginmobile {
            top: 0px;
        }
    }
}
@-webkit-keyframes fixedheader {
    0%   { top: -100%; }
    100% { top: 0; }
}
@-moz-keyframes fixedheader {
0%   { top: -100%; }
100% { top: 0; }
}
@-o-keyframes fixedheader {
0%   { top: -100%; }
100% { top: 0; }
}
@keyframes fixedheader {
0%   { top: -100%; }
100% { top: 0; }
}
.siteNavigation {
    display: flex;
    flex-direction: row;
    position:relative;
    padding-right: 45px;
    justify-content: flex-end;
    > ul {
        list-style: none;
        margin: 0 0 0 0;
        padding: 0 0 0 0;
        display: flex;
        flex-direction: row;
        > li {
            padding-right:60px;
            position: relative;
            a {
                color: #fff;
                font-size: 15px;
                text-transform: uppercase;
                line-height: 27px;
                display: block;
                @include fmedium();
                &:hover {
                    text-decoration: none;
                }
            }
            &.active,
            &:focus,
            &:active,
            &:hover {
                > a {
                    color: #f3b329;
                    
                }
            }
            
        }
        .sub-menu {
            background: rgba(13,27,42, 0.5);
            border-radius: 3px;
            padding: 10px 0 10px 10px;
            box-shadow: 0px 1px 3px 0 rgba(0, 0, 0, 0.5);
            position:absolute;
            width: 425px;
            top: 170%;
            left: 0;
            display: none;
            
        }
        ul {
            margin: 0 0 0 0;
            padding: 0 0 0 0;
            list-style: none;
            display: block;
            width: 100%;
            margin-left: -10px;
            &:before,
            &:after  {
                clear: both;
            }
            &:after {
                display: table;

            }
            li {
                width: calc(50% - 10px);
                float: left;
                margin-left: 10px;
                a {
                    padding: 5px 10px;
                    text-transform: none;
                }
                &:hover {
                    a {
                        background: #fff;
                        border-radius: 3px;
                        color: #818B96;
                    }
                }
            }
        }
    }
    &.mobileMenu {
        position: fixed;
        left:0;
        top:0;
        bottom: 0;
        right: 0;
        background: #000;
        display: block;
        z-index: 99;
        padding: 100px 15px 25px;
        overflow-y: scroll;
        overflow-x: hidden;
        ul {
            display: block;
            margin-left: 0;
            li {
                margin-left: 0;
                margin-bottom: 50px;
                
                &.active {
                    .sub-menu {
                        display: block;
                    }
                    a {
                        &:after {
                            background: url('../images/icon/chevronup.svg') no-repeat right top;
                            background-size: 100%;
                        }  
                    }
                }
                a {
                    font-size: 24px;
                    position: relative;
                    padding-bottom: 0;
                    &:after {
                        background: url('../images/icon/chevron.svg') no-repeat right top;
                        background-size: 100%;
                        content: '';
                        width: 22px;
                        display: block;
                        height: 12px;
                        position:absolute;
                        right: 0;
                        top: 5px;
                    }
                    
                }
                &.nosub {
                    a {
                        &:after {
                            display:none;
                        }
                    }
                }
                .sub-menu {
                    background: transparent;
                    position: relative;
                    padding: 20px 0 0 0;
                    width: 100%;
                    ul {
                        width: 100%;
                        &:before,
                        &:after {
                            content: ''
                        }
                        li {
                            width: 100%;
                            margin-bottom: 10px;
                            a {
                                padding: 10px 0;
                                font-size: 20px;
                                color: #A6A6A6;
                                @include flight();
                                &:after {
                                    display:none;
                                }
                            }
                        }
                    }
                }
            }
        }
        .siteSearch {
            width: 100%;
            margin-left: 0;
            .clsoebtn {
                display:none;
            }
            .swrap {
                border-bottom: 1px solid #fff;
            }
        }
    }
}

input,
button {
    &:focus {
        outline: 0;
        box-shadow: none;
    }
}

.siteSearch {
    margin-left: 60px;
    width: 35px;
    position:absolute;
    right: 0;
    top:-7px;
    transition: all ease 0.2s;
    
    &.active {
        width: 500px;
        .swrap {
            border-bottom-color: #fff;
        }
        .searchFielfds {
            
        }
    }
    .swrap {
        display: flex;
        border-bottom: 1px solid transparent;
        justify-content: center;
        align-items: baseline;
        padding-bottom: 5px;
        width: 100%;
    }
    .clsoebtn {
        padding: 0 10px;
        width: 14px;
    }
    .searchinput {
        background: transparent;
        border:0;
        margin-left: 20px;
        @include flight();
        font-size: 22px;
        width: calc(100% - 49px);
        outline: 0;
        color: #fff;
        &::placeholder {
            color: rgba(244, 244, 244, 0.5);
        }
        &:focus {
            outline: 0;
            box-shadow:none;
        }
    }
    .searchFielfds {
        width: calc(100% - 49px);
        transition: all ease 0.2s;
        display:flex;
        overflow: hidden;
    }
    .sbtn {
        background: transparent;
        border:0;
        cursor: pointer;
    }
}



.loginmobile{
    background: #f4b400;
    position: relative;
    border-radius: 50px;
    border: 100px;
    height: 36px;
    width: 36px;
    z-index: 100;
    display: none;
    img{
        position: absolute;
        left: 10px;
        top: 8px;
        width: 15px;
    }
}

@media (min-width: 993px) {
    .flexBox {
        width: 90%;
    }
    .siteNavigation {
        margin-top: 3px;
        float: right;
        > ul {
            > li {
                padding-right: 40px;
                &.active,
                &:hover {

                    .sub-menu {
                        display: block;
                    }
                    &:after {
                        content: '';
                        height: 20px;
                        display: block;
                        position: fixed;
                        left:0;
                        right: 0;
                        z-index: 2;
                    }
                }
                .sub-menu {
                    &:hover {
                        display: block;
                    }
                }
            }
        }
    }
    .siteSearch {
        // margin-left: 40px;
        right: 35px;
    }
}

@media (min-width: 1100px) {
    .flexBox {
        width: 95%;
    }
}

@media (min-width: 1200px) {
    .flexBox {
        width: 92%;
    }
    .siteNavigation { 
        > ul {
            > li {
                padding-right: 50px;
            }
        }
    }
    .siteSearch {
        // margin-left: 50px;
        right: 15px;
    }
}
@media (min-width: 1300px) {
    .flexBox {
        width: 93%;
    }
    .siteNavigation {
        margin-right: 0;
        > ul {
            > li {
                padding-right: 60px;
            }
        }
    }
    .siteSearch {
      
        right: 0;
    }
}

@media (min-width: 1350px) {
    .flexBox {
        width: 97%;
    }
}

@media (min-width: 1400px) {
    .flexBox {
        width: 100%;
    }
}

@media (max-width: 992px) {
    .siteSearch {
        position: relative;
        .searchFielfds {
            width: 100%;
        }
    }
    .siteNavigation {
        display: none;
        margin-right: 0;
        &.mobileMenu {
            padding-left: 40px;
            padding-right: 40px;
        }
    }
    .siteHeader {
        .hamburgers {
            display:block;
            position: relative;
            z-index: 100;
        }
        .siteLogo {
            position:relative;
            z-index: 100;
            img {
                width: 208px;
            }
        }
        
        .siteLogin {
            padding: 0 00px 0 30px;
            display: none;
            span {
                display:none;
            }
        }
    }
    .mobileWrapper {
       float: right;
       display: flex;
    }
    .loginmobile {
        display: block;
        margin-top: -5px;
        margin-right: 20px;
    }
}

@media (max-width: 767px) {
    .siteNavigation {
        
        &.mobileMenu {
            padding-left: 15px;
            padding-right: 15px;

            ul{
                ul{
                    li{
                        &:hover{
                            a{
                                background: transparent;
                            }   

                        }
                        
                    }
                }
                li {
                    padding-right: 10px;

                    &:hover{
                        a{
                            color: $white;
                        }
                    }
                    
                    &.active{
                        a{
                            color: $yellow;
                        }
                    }
                    
                    .sub-menu {
                        ul {
                            li {
                                a {
                                    &:hover {
                                        color: #f3b329;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            
        }
    }
}

// header for inner pages

.siteHeader{
    &.innerHeader{
        background: $white;
        min-height: 74px;
        padding: 24px 0 0;
        box-shadow: 1px 1px 1px #e8e8e8;

        .siteLogin{
            top: 9px;
        }

        @include respond-to('from-large'){
            .siteNavigation{
                >ul{
                    >li{
                        a{
                            color: #818B96;
                        }

                        &.active,
                        &:focus,
                        &:active,
                        &:hover {
                            > a {
                                color: #f3b329;
                                
                            }
                        }
                    }

                    ul{
                        li{
                            a{
                                color: $white;
                            }

                            &:hover{
                                a{
                                    color: #818b96;
                                }
                            }
                        }
                    }
                }
            }

            .siteSearch{

                .searchinput {
                    color: #818b96;
                    &::placeholder {
                        color: rgba(214, 214, 214, 0.8);
                    }
                }


                &.active{
                    .swrap{
                        border-bottom-color: #818b96;
                    }
                }
            }
        }

        .hamburger-inner,
        .hamburger-inner::after,
        .hamburger-inner::before{
            background-color: #818B96;
        }
    }
}