
.galleryWrap{

}

.news-item{
    
    &-link{
        margin-bottom: 30px;
        position: relative;
        display: block;

        .imgholder{
            position: relative;
            overflow: hidden;

            &:after{
                background: -moz-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(183,191,199,0.65) 100%);
                background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%,rgba(183,191,199,0.65) 100%);
                background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(183,191,199,0.65) 100%);
                filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#a6b7bfc7',GradientType=0 );
                content: '';
                height: 100%;
                position: absolute;
                left: 0;
                bottom: 0;
                width: 100%;
                opacity: .8;
            }


            .img{
                width: 100%;
            }

            .date{
                position: absolute;
                bottom: 10px;
                left: 10px;
                z-index: 1;
                color: $white;
            }   
        }

        .title{
            margin-top: 15px;
            @include fmedium;
            color: $copy;
        }

        &:hover{
            text-decoration: none;

            .title{
                color: $yellow;
            }
        }
    }
}