#mainLayout {
    height: 100vh;
    position:relative;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
    .sectionImage {
        position:absolute;
        right:0;
        top:0;
        left: 0;
        z-index: -1;
        min-width: 100%;
        min-height: 100%;
        background-size: cover !important;
    }
    .sectionImageMobile {
        display: none;
        position:absolute;
        top:0;
        z-index: -1;
        height: 100%;
        left: 0;
        width: 100%;
        background-size: cover !important;
    }
    .contentBox {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .sectionTitle {
        font-size: 42px;
        color: #fff;
        @include flight();
        font-weight: normal;
        letter-spacing: -0.28px;
        line-height: 50px;
        text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.34);
        margin: 0 0 200px 0;
        width: 50%;
        text-align: center;
    }
    .siteStats {
        display:block;
        margin: 0 0 150px 0;
        padding: 0 0 0 0;
        list-style: none;
        justify-content: space-between;
        align-items: center;
        align-self: baseline;
        width: 100%;
        &:before,
        &:after {
            content: '';
            clear:both;
        }
        &:after {
            display: table;
        }
        li {
            font-size: 16px;
            color: #fff;
            text-align: center;
            width: 25%;
            float: left;
            @include fmedium();
            span {
                font-size: 40px;
                margin-bottom: 5px;
                display: block;
                line-height: 40px;
            }
        }
    }
    .scrollIndicator {
        position: absolute;
        left: 50%;
        bottom: 30px;
        color: #fff;
        font-size: 15px;
        transform: translateX(-50%);
        display: flex;
        flex-direction: column;
        img {
            margin: 0 auto 10px;
            max-width: 22px;
            width: 22px;
        }
        &:hover {
            text-decoration: none;
        }
    }
}

@media (max-width: 767px) {
    #mainLayout {
        .sectionImage {
            display: none;
        }
        .sectionImageMobile {
            display: block;
        }
        .sectionTitle {
            margin-bottom: 70px;
            width: 100%;
        }
        .siteStats {
            li {
                font-size: 12px;

                span {
                    font-size: 20px;
                    line-height: 22px;
                }
            }
        }
    }
}