.sideNavigation{
    @extend .section-top-padding;
    max-width: 284px;

    @include respond-to('medium'){
        max-width: 100%;
    }

    ul{
        list-style: none;
        padding-left: 0px;
        box-shadow: 0 0 8px 0 #e4e4e4;
        border-radius: 5px;
        overflow: hidden;

        li{
            a{
                padding: 15px 24px;
                position: relative;
                display: block;
                color: $copy;
                @include fmedium;

                &:after{
                    background: url('../images/icon/right-arrow-sidebar.svg') no-repeat center;
                    transform: translateY(-50%);
                    background-size: 100%;
                    position: absolute;
                    height: 15px;
                    content: '';
                    right: 20px;
                    width: 8px;
                    top: 50%;
                    display: none;
                }
            }

            &:first-child{
                border-top: 5px solid $yellow;
                margin-bottom: 5px;

                a{
                    padding: 22px 24px 18px;
                    font-size: 24px;

                    &:before{
                        background: #D8D8D8;
                        position: absolute;
                        height: 1px;
                        width: calc(100% - 48px);
                        bottom: 0;
                        left: 24px;
                        content: '';
                    }
                }
            }

            &.active{
                background: rgba(136, 124, 104, .18);

                a{
                    &:after{
                        display: block;
                    }
                }
            }
        }
    }

    // styling for mobile
    @include respond-to('medium'){
        transition: max-height .4s ease-out;
        overflow: hidden;
        padding-top: 0px;
        max-height: 0;
        box-shadow: 0 0 8px 0 #e4e4e4;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;

        ul{
            border-top-left-radius: 0px;
            border-top-right-radius: 0px;
            box-shadow: none;

            li{
                &:first-child{
                    border-top: 0px;
                    margin-bottom: 0px;

                    a{
                        padding:15px 24px;
                        font-size: 17px;

                        &:before{
                            display: none;
                        }
                    }
                }

                &.active{
                    display: none;
                }
            }
        }
    }
}


.mobileAccordionHolder{
    background: rgba(136, 124, 104, .19);
    margin-top: 34px;
    padding: 15px 24px;
    border-radius: 5px;
    @include fmedium;
    position: relative;
    display: none;

    @include respond-to('medium'){
        display: block;
    }


    &:before{
        width: 14px;
        height: 2px;
        background: #414850;
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 25px;
        border-radius: 5px;
    }

    &:after{
        height: 14px;
        width: 2px;
        background:#414850;
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 31px;
        border-radius: 5px;
    }

    &.active{
        border-bottom-left-radius: 0px;
        border-bottom-right-radius: 0px;

        &:after{
            opacity: 0;
        }

        + .sideNavigation{
            max-height: 500px;
        }
    }
}