.download-section{
    @extend .section-top-padding;
    @extend .section-bottom-margin;

    @include respond-to('medium'){
        padding-top: 38px;
    }
}

.listingygrid{
    margin-left: -5px;
    margin-right: -5px;
    .col-lg-4, .col-sm-6{
        padding-left: 5px;
        padding-right: 5px;
    }
}

.download-item{
    margin-bottom: 30px;

    .imgholder{
        position: relative;
        overflow: hidden;

        .img{
            width: 100%;
        }

        .pdf{
            position: absolute;
            top: 10px;
            left: 10px;
        }

        .hoverHolder{
            background: rgba(13, 27, 42, .70);
            position: absolute;
            height: 100%;
            width: 100%;
            left: 0;
            top: 0;

            transition: transform .4s;
            transform: translateY(100%);

            .link{
                transform: translateY(-50%);
                text-decoration: none;
                text-align: center;
                position: absolute;
                @include fmedium;
                font-size: 14px;
                color: $white;
                width: 50%;
                top: 50%;
                left: 0%;

                + .link{
                    left: 50%;
                }

                .icon{
                    margin-bottom: 10px;
                    display: block;
                }

                &:hover{
                    color: $yellow;

                    svg{
                        g{
                            fill: $yellow;
                        }
                    }
                }
            }
        }

        &:hover{
            .hoverHolder{
                transform: translateY(0%);
            }
        }
    }

    .title{
        margin-top: 15px;
        @include fmedium;
        color: #64615b;
    }
}