.selectWrap{

    .select2-container {

        width: 100% !important;
        @include respond-to('medium') {
            width: 100% !important;
        }

        &.select2-container--open{
            box-shadow: 0 0 8px 0 #e4e4e4;
        }

        .select2-selection {
            &--single {
                height: 50px;
                border-radius: 0;
                border-color: #D2D2D2;
                border-top: 0;
                border-left: 0;
                border-right: 0;

                &:focus {
                    outline: none;
                    border-color: $orange;
                }

                .select2-selection__rendered {
                    line-height: 50px;
                    padding-left: 20px;
                }

                
                .select2-selection__arrow {
                    height: 50px;
                    position: absolute;
                    top: 50%;
                    right: 15px;
                    width: 20px;
                    transform: translateY(-50%) rotate(90deg);
                    transition: 300ms all ease-in-out;
                    background: url('../images/icon/select-arrow.svg') no-repeat center;
                    background-size: 8px 15px;

                    b {
                        
                        border: 0;
                    }
                }
            }
        }

        &--open {
            .select2-selection--single .select2-selection__arrow {
                transform: translateY(-50%) rotate(-90deg);
                
            }

            .select2-selection--single {
                border-color: $orange;
            }
        }

        &.select2-container--disabled {
            
            .select2-selection--single {
                cursor: not-allowed;
                background-color: $gray;
            }
        }
    }
}

// global fixes

.select2-dropdown{
    box-shadow: 0 0 8px 0 #e4e4e4;
    border: none;
}

.select2-search--dropdown{
    display: none;
}

.select2-container--default .select2-results__option--highlighted[aria-selected],
.select2-container--default .select2-results__option[aria-selected=true]{
    background: rgba(136,124,104,.18);
    color: $copy;
}


// form style elements style

.form-style{
    .form-group{
        margin-bottom: 35px;

        &.selectWrap{
            .error{
                position: absolute;
                bottom: 35px;
                left: 15px;

                @include respond-to ('medium'){
                    bottom: 0px;
                }
            }
        }

        &.captcha{
            margin-bottom: 0px;
            .g-recaptcha{
                text-align: center;
                margin: 0 auto;
                width: 304px;
            }
        }
    }

    .form-label{
        margin-bottom: 20px;
        @include fmedium;
        font-size: 18px;
        display: block;
    }

    .form-control{
        height: 50px;
        border-radius: 0;
        border-color: #d2d2d2;
        border-color: #d2d2d2;
        border-top: none;
        border-right: none;
        border-left: none;

        &:focus{
            border-color: #f59131;
            box-shadow: none;
        }

    }

    textarea{
        &.form-control{
            height: 80px;
        }
    }
    .error{
        color: red;
        font-size: 14px;
    }

    .submitBtn{
        text-align: center;
        margin: 30px 0 46px;

        .btn-primary{
            
            min-width: 152px;
        }
    }
}
