.gallery-section{
    @extend .section-top-padding;
    @extend .section-bottom-margin;

    @include respond-to('medium'){
        padding-top: 38px;
    }
}

.galleryWrap{
    
}


.gallerygrid{
    margin-left: -5px;
    margin-right: -5px;
    .col-lg-4, .col-sm-6{
        padding-left: 5px;
        padding-right: 5px;
    }
}
.gallery-item{
    
    &-link{
        margin-bottom: 30px;
        position: relative;
        display: block;

        .img{
            width: 100%;
        }

        .title{
            margin-top: 15px;
            @include fmedium;
            color: $copy;
        }

        .playicon{
            transform: translate(-50% , -50%);
            position: absolute;
            margin-top: -15px;
            left: 50%;
            top: 50%;
            opacity: .8;
            transition: ease .2s;

        }

        &:hover{
            text-decoration: none;

            .title{
                color: $yellow;
            }

            .playicon{
                transform: translate(-50% , -50%) scale(1.1);
                opacity: 1;
            }
        }
    }
}


.fullscreen-overlay {
    top: 0 !important;
    right: 0 !important;
    left: 0 !important;
    bottom: 0 !important;
    position: absolute !important;
    background-color: #000000 !important;
    width: 100% !important;
    height: 100% !important;
    margin: auto !important;
  }
  
  .fullscreen-overlay .fancybox-close {
    /*rtl:ignore */
    right: 0 !important;
    top: 0 !important;
  }
  
  .fullscreen-overlay .fancybox-inner {
    width: 100% !important;
  }
  
  .fullscreen-overlay .fancybox-skin {
    padding: 0 !important;
    box-shadow: none;
  }